import $ from "jquery";
export default {
  data() {
    return {
      widths: "",
      tableHead: [
        "采购单号",
        "生产单号",
        "工厂型体",
        "物料代码",
        "物料名称",
        "尺码",
        "数量",
        "单价（不含税）",
        "金额"
      ],
      tableData: [
        [
          "450004896",
          "DD182151G-1",
          "090800275",
          "DD182151g-13鞋桶成",
          "123",
          ["500", "500", "500", "500", "500", "500", "500", "500", "500"],
          "皮革",
          "双",
          "1500"
        ],
        [
          "450004896",
          "DD182151G-1",
          "090800275",
          "DD182151g-13鞋桶成",
          "123",
          ["500", "500", "500", "500", "500", "500", "500", "500", "500"],
          "皮革",
          "双",
          "1500"
        ],
        [
          "450004896",
          "DD182151G-1",
          "090800275",
          "DD182151g-13鞋桶成",
          "123",
          ["500", "500", "500", "500", "500", "500", "500", "500", "500"],
          "皮革",
          "双",
          "1500"
        ]
      ]
    };
  },
  computed: {
    tableStyle() {
      return {
        'width': '270px !important'
      }
    }
  },
  components: {},
  mounted() {
    // alert(this.tableHead.length)
    this.widths = ((this.tableHead.length-1) * 120) + 270;
    this.$nextTick(() => {
      this.init();
    });
  },
  methods: {
    change() {},
    // 等待数据加载完之后初始化表格
    init() {
      var win = $(window),
        scrollAreaEl = $(".t_r_content"),
        leftFreezeEl = $(".t_l_freeze"),
        leftTableEl = leftFreezeEl.find("table"),
        rightTableEl = $(".t_r_t table");

      //动态计算容器最大高度
      function adjustHeight() {
        var winHeight = win.height() / 2,
          tableHeight = 3 * 40; // 3为表格数据总长度
        leftFreezeEl.height(tableHeight);
        scrollAreaEl.height(tableHeight );
      }

      adjustHeight();
      win.on("resize", adjustHeight);

      //设置iscroll
      var myScroll = new IScroll(".t_r_content", {
        scrollX: true,
        scrollY: true,
        probeType: 3
      });

      //阻止默认滚动
      scrollAreaEl.on("touchmove mousewheel", function(e) {
        e.preventDefault();
      });

      //固定上左表头的滚动
      myScroll.on("scroll", updatePosition);
      myScroll.on("scrollEnd", updatePosition);

      function updatePosition() {
        var a = this.y;
        var b = this.x;
        leftTableEl.css(
          "transform",
          "translate(0px, " + a + "px) translateZ(0px)"
        );
        rightTableEl.css(
          "transform",
          "translate(" + b + "px, 0px) translateZ(0px)"
        );
      }
    }
  }
};



